import { Injectable } from '@angular/core';

import { AngularFireDatabase } from 'angularfire2/database';

import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators/map';

interface IAddProductsToPurchasesVariables {
  uid: string;
  productsKey: string;
}

interface IGetPurchasesVariables {
  uid: string;
  limitLast?:number;
}

export interface IPurchaseData{
  productKey: string;
  timestamp: number;
}

/*
* Serviço PurchaseService
* - Adiciona e obtém produtos comprados pelo usuário no banco de dados
* */
@Injectable()
export class PurchaseService{
  constructor(
    private db: AngularFireDatabase
  ){}

  addProductsToPurchases(
    addProductsToPurchasesVariables:IAddProductsToPurchasesVariables
  ):PromiseLike<any>{
    const { uid, productsKey } = addProductsToPurchasesVariables;
    const key:string = this.db.createPushId();

    return this.db.list(`users/${uid}/purchases/`)
      .push({
        productsKey,
        timestamp: Date.now()
      });
  }

  getProductsFromPurchases(
    getPurchasesVariables:IGetPurchasesVariables
  ): Observable<IPurchaseData[]>{
    const { uid, limitLast=5 } = getPurchasesVariables;

    return this.db.list(`users/${uid}/purchases`)
      .valueChanges()
      .pipe(
        map((purchases=[]) => {
          const products = [];

          purchases.forEach((purchase:any) => {
            const purchaseProductsKey = purchase.productsKey.trim().split(' ');
            purchaseProductsKey.forEach(purchaseProductKey => {
              products.push({
                productKey: purchaseProductKey,
                timestamp: purchase.timestamp
              });
            });
          });

          return products.sort((product1,product2) => {
            return product2.timestamp - product1.timestamp;
          }).slice(0,limitLast);
        })
      );
  }
}



