import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { AngularFireAuth } from 'angularfire2/auth';

import { CartService } from '../../services';

import { ISubscription } from 'rxjs/Subscription';

@Component({
  selector: '[cf-button-cart]',
  templateUrl: 'cf-button-cart.html'
})
export class CfButtonCartComponent {
  productsQty: number = 0;

  getAllSkusSub: ISubscription;

  constructor(
    public angularFireAuth: AngularFireAuth,
    public cartService: CartService,
    public navCtrl: NavController
  ) {}

  ngOnInit(){
    this.getProductsQty();
  }

  ngOnDestroy(){
    if(this.getAllSkusSub){
      this.getAllSkusSub
        .unsubscribe();
    }
  }

  private getProductsQty(){
    this.getAllSkusSub = this.cartService
      .getAllSkus()
      .subscribe(
        products => {
          this.productsQty = products.length;

        }, error => console.log(error)
      );
  }

  goToCartPage(){
    this.navCtrl.push('CartStep1Page');
  }
}



