import { Injectable } from '@angular/core';

@Injectable()
export class validationMessageProvider{
  readonly equalityErrorMessages = {
    generic : 'Nome já utilizado.',
    gymBranchName : 'Nome já utilizado.'
  };

  readonly requiredErrorMessages = {
    generic : 'Preenchimento necessário.',
    gymRegisterAuthorization : 'Você não pode criar este perfil sem autorização.',
    radioElement : 'Escolha pelo menos uma das opções.'
  };

  readonly patternErrorMessages = {
    generic : 'Valor informado mal formatado.',
    email : 'Email inválido.'
  };

  getExceedingCharacters(errors: any): number{
    const maxLengthError: any = errors && errors.maxlength;
    let exceedingCharacters: number = 0;

    if(maxLengthError){
      exceedingCharacters = maxLengthError.actualLength - maxLengthError.requiredLength;
    }
    return exceedingCharacters;
  }

  getMissingCharacters(errors: any): number{
    const minLengthError: any = errors && errors.minlength;
    let missingCharacters: number = 0;

    if(minLengthError){
      missingCharacters = minLengthError.requiredLength - minLengthError.actualLength;
    }
    return missingCharacters;
  }

  hasEqualityError (errors: any):boolean{
    return errors && !!errors.equality;
  }

  hasMaxLengthError (errors: any):boolean{
    return errors && !!errors.maxlength;
  }

  hasMinLengthError (errors: any):boolean{
    return errors && !!errors.minlength;
  }

  hasPatternError (errors: any): boolean{
    return errors && !!errors.pattern;
  }

  hasRequiredError (errors: any): boolean{
    return errors && !!errors.required;
  }
}



