export interface ILimits{
  readonly email: {
    readonly maxLength : number;
  };

  readonly password: {
    readonly minLength : number;
    readonly maxLength : number;
  };
}

export const limitsHelperObject: ILimits = {
  email: {
    maxLength: 256
  },
  password: {
    minLength: 6,
    maxLength: 64
  }
};




