import { Component, HostBinding, HostListener, Input } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';

import { validationMessageProvider } from './cf-validation-messages.provider';

/*
* Componente CfValidationMessagesComponent
* - Mensagens de validação de campos de formulário
* */
@Component({
  selector: 'cf-validation-messages',
  templateUrl: 'cf-validation-messages.html',
  providers: [
    validationMessageProvider
  ]
})
export class CfValidationMessagesComponent{
  // Show Errors
  showEqualityError: boolean = false;
  showMaxLengthError: boolean = false;
  showMinLengthError: boolean = false;
  showPatternError: boolean = false;
  showRequiredError: boolean = false;

  // Element ID
  idClass:string = `idClass-${Math.round(Math.random() * 10E14)}`;

  // Caracteres
  exceedingCharactersNumber: number;
  missingCharactersNumber: number;

  // Messages
  equalityErrorMessage: string;
  patternErrorMessage: string;
  requiredErrorMessage: string;

  // Form
  private formControl: AbstractControl;
  @Input()
  private formGr: FormGroup;
  @Input()
  private formCtrlName: string;
  @Input()
  private equalityError: string;
  @Input()
  private patternError: string;
  @Input()
  private requiredError: string;

  @HostBinding('class.cf-c-has-validation-error')
  private hasValidationError: boolean;

  constructor(
    private validation: validationMessageProvider
  ){}

  @HostListener('body:submit', ['$event']) onFormSubmit($event){
    const hasValidationEl = $event.target.innerHTML.includes(this.idClass);

    if(hasValidationEl){
      this.runRequiredValidation();
    }
  }

  ngOnInit(){
    this.formControl = this.formGr.controls[this.formCtrlName];

    this.formControl.valueChanges.subscribe(() => {
      this.runAllValidations();
    });
  }

  private runRequiredValidation(){
    const formControlErrors = this.formControl.errors;

    if(this.validation.hasRequiredError(formControlErrors)){
      const genericMessage = this.validation.requiredErrorMessages.generic;
      const customMessage = this.requiredError && this.validation.requiredErrorMessages[this.requiredError];
      this.requiredErrorMessage = customMessage ||genericMessage;

      this.showRequiredError = true;
    } else {
      this.showRequiredError = false;
    }
  }

  private runAllValidations(){
    const formControlErrors = this.formControl.errors;

    this.hasValidationError = !!formControlErrors;

    // Equality Error
    // ************************

    if (this.validation.hasEqualityError(formControlErrors)){
      const genericMessage = this.validation.equalityErrorMessages.generic;
      const customMessage = this.equalityError && this.validation.equalityErrorMessages[this.equalityError];
      this.equalityErrorMessage = customMessage || genericMessage;

      this.showEqualityError = true;
    } else {
      this.showEqualityError = false;
    }

    // MaxLength Error
    // ************************
    if (this.validation.hasMaxLengthError(formControlErrors)){
      this.exceedingCharactersNumber = this.validation.getExceedingCharacters(formControlErrors);
      this.showMaxLengthError = true;
    } else {
      this.showMaxLengthError = false;
    }

    // MinLength Error
    // ************************
    if (this.validation.hasMinLengthError(formControlErrors)){
      this.missingCharactersNumber = this.validation.getMissingCharacters(
        formControlErrors
      );
      this.showMinLengthError = true;
    } else {
      this.showMinLengthError = false;
    }

    // Pattern Error
    // ************************
    if (this.validation.hasPatternError(formControlErrors)){
      const genericMessage = this.validation.patternErrorMessages.generic;
      const customMessage = this.patternError && this.validation.patternErrorMessages[this.patternError];
      this.patternErrorMessage = customMessage || genericMessage;

      this.showPatternError = true;
    } else {
      this.showPatternError = false;
    }

    // Required Error
    // ************************
    this.runRequiredValidation();
  }
}



