'use strict';

import { NgModule } from '@angular/core';

import {
  ErrorHandlerHelper,
  limitsHelperObject,
  regexHelperObject
} from './';
import { limitsHelper, regexHelper } from '../app/app.tokens';


@NgModule({
  providers: [
    ErrorHandlerHelper,
    {
      provide: limitsHelper,
      useValue: limitsHelperObject
    },
    {
      provide: regexHelper,
      useValue: regexHelperObject
    },
  ]
})
export class HelpersModule{}




