import { Injectable } from '@angular/core';

import { AngularFireDatabase } from 'angularfire2/database';

import { IProductExisting } from '../interfaces';

import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';

@Injectable()
export class ProductService{
  constructor(
    private db: AngularFireDatabase
  ){}

  create({ companyKey, name, description, price, category, sale }):Promise<string>{
    const key:string = this.db.createPushId();

    return this.db.list(`products`)
      .set(key, { companyKey, key, name, description, price, category, sale })
      .then(() => key );
  }

  getAllOnSale(): Observable<any>{
    return this.db.list(
      '/products',
      ref => ref.orderByChild('sale').equalTo(true)
    ).valueChanges();
  }

  getAllOfACompany(companyKey:string): Observable<IProductExisting[]>{
    return this.db.list(
        '/products',
        ref => ref.orderByChild('companyKey').equalTo(companyKey)
      )
      .valueChanges()
      .pipe(
        map((products: IProductExisting[]) => {
          if(!products){ return []; }
          return products;
        })
      );
  }

  getOne(productKey:string): Observable<any>{
    return this.db.object(`products/${productKey}`)
      .valueChanges();
  }

  removeOne(productKey:string): Promise<any>{
    return this.db.object(`products/${productKey}`)
      .update({
        hide: true
      });
  }

  updateOne({ productKey, name, description, price, category, sale }):Promise<any>{
    return this.db.object(`products/${productKey}`)
      .update({ name, description, price, category, sale });
  }
}



