import { Injectable } from '@angular/core';

import { Storage } from '@ionic/storage';

import { Subject } from 'rxjs/Subject';
import { Observable } from 'rxjs/Observable';
import { map, mergeMap } from 'rxjs/operators';

@Injectable()
export class CartService{
  private readonly cartStorageKey: string = 'cart';
  private cartContentSubject:Subject<string[]> = new Subject();

  constructor(
    private storage: Storage
  ){}

  private async getCartContent():Promise<string[]>{
    const cartContent = await this.storage.get(this.cartStorageKey);
    const isArray = Array.isArray(cartContent);

    if(!isArray) { return []; }

    return cartContent;
  }

  private async setCartContent(cartContent:string[]):Promise<any>{
    await this.storage.set(this.cartStorageKey, cartContent);

    this.cartContentSubject.next(cartContent);

    return;
  }

  private removeCartContent():Promise<any>{
    return this.setCartContent([]);
  }
  // Sku
  // ******************************************
  async addOneSku(sku:string):Promise<any>{
    const cartContent = await this.getCartContent();
    cartContent.push(sku);

    return this.setCartContent(cartContent);
  }

  async removeOneSku(sku:string):Promise<any>{
    const cartContent = await this.getCartContent();

    const index = cartContent.indexOf(sku);

    if (index !== -1) {
      cartContent.splice(index, 1);
    }

    return this.setCartContent(cartContent);
  }

  async isSkuinCart(sku:string):Promise<boolean>{
    const cartContent = await this.getCartContent();

    return !!(cartContent.indexOf(sku) + 1);
  }

  // Skus
  // *******************************************
  getAllSkus():Observable<string[]>{
    const cartContentSubject = this.cartContentSubject;
    this.getCartContent()
      .then(cartContent => {
        cartContentSubject.next(cartContent);
      });

    return cartContentSubject.asObservable();
  }

  async removeSku(sku:string):Promise<any>{
    const cartContent = await this.getCartContent();

    const newCartContent = cartContent.filter(skuFromCart => {
      return skuFromCart !== sku;
    });

    return this.setCartContent(newCartContent);
  }

  emptyCart():Promise<any>{
    return this.removeCartContent();
  }
}



