import { Injectable } from '@angular/core';

import { AngularFireDatabase } from 'angularfire2/database';

import { ICompany, ICompanyCreation, ICompanyEdition } from '../interfaces';

import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';

@Injectable()
export class CompanyService{
  constructor(
    private db: AngularFireDatabase
  ){}

  create(companyToCreate:ICompanyCreation):Promise<any>{
    const key:string = this.db.createPushId();
    const company = Object.assign(companyToCreate);

    company.key = key;

    return this.db.list('companies')
      .set(key, company)
      .then(() => key );
  }

  getAll(): Observable<ICompany[]>{
    return this.db.list('companies')
      .valueChanges()
      .pipe(
        map((companies: ICompany[]) => {
          if(!companies){ return []; }
          return companies;
        })
      );
  }

  getOne(key): Observable<ICompany | {}>{
    return this.db.object(`companies/${key}`)
      .valueChanges()
      .pipe(
        map((company: ICompany) => {
          if(!company){ return {}; }
          return company;
        })
      );
  }

  updateOne(key:string, editCompany:ICompanyEdition):Promise<any>{
    return this.db.object(`companies/${key}`)
      .update(editCompany);
  }

  removeOne(companyKey:string): Promise<any>{
    return this.db.object(`companies/${companyKey}`)
      .update({
        hide: true
      });
  }
}



